.App {
  font-family: Arial, Helvetica, sans-serif;
  border: solid gold;
  border-style: ridge;
  border-radius: 8px;
  padding: 10px;
  width: 400px;
  background-color: black;
}

h1 {
  color: white;
  text-align: center;
}

button {
  background-color: gold;
  border: solid gold;
  border-radius: 8px;
  padding: 3px;
  margin-right: 5px;
  margin-left: 5px;
  margin-top: 20px;
  cursor: pointer;
}

input {
  background-color: lemonchiffon;
  border: solid lemonchiffon;
  border-radius: 8px;
  text-align: center;
  width: 380px;
  padding: 3px;
}

ul {
  color: silver;
  font-size: 19px;
  cursor: pointer;
}
